/* Back to Top Button */
.back-to-top {
  position: fixed;
  bottom: 70px; /* Increase to move it farther above the footer */
  right: 20px;
  background: #008080;
  color: #fff;
  border: none;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  font-size: 20px;
  cursor: pointer;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  display: none; /* Initially hidden */
  align-items: center;
  justify-content: center;
  transition: opacity 0.3s ease-in-out, transform 0.3s ease-in-out;
  z-index: 9999; /* Ensure it's above the footer */
}

/* Show button when scrolled */
.back-to-top.show {
  display: flex;
  opacity: 1;
  transform: scale(1);
}

/* Hover effect */
.back-to-top:hover {
  background-color: #005959;
}

/* Responsive Adjustments */
@media (max-width: 480px) {
  .back-to-top {
    width: 40px;
    height: 40px;
    font-size: 18px;
    bottom: 60px; /* Increase offset for smaller screens as well */
    right: 15px;
  }
}