/* ===================================
   1) RESET & BASE BODY STYLES
   =================================== */
html, body {
  margin: 0;
  padding: 0;
  /* Remove body display flex here 
     if you're relying on the .App container 
     for your layout. */
}

body {
  font-family: 'Arial', sans-serif;
  color: white;
  scroll-behavior: smooth;

  /* If the .App container handles layout, you can remove these:
     display: flex; 
     flex-direction: column; 
     position: relative; 
     min-height: 100vh; 
     overflow-x: hidden; 
  */

  /* Keep overflow-x: hidden if your animations 
     might cause horizontal scrolling. */
  overflow-x: hidden;
}

/* ===================================
   2) BACKGROUND EFFECTS
   =================================== */
body::before {
  content: "";
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, #2e2e2e, #1a1a1a, #3d3d3d, #262626);
  background-size: 400% 400%;
  animation: metallicShift 10s infinite alternate ease-in-out;
  z-index: -2; /* behind everything */
}

body::after {
  content: "";
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: radial-gradient(circle, rgba(255,255,255,0.05) 1%, transparent 70%);
  background-size: 50px 50px;
  animation: gridGlow 6s infinite alternate ease-in-out;
  z-index: -1; /* above the gradient, below content */
}

/* Keyframes remain the same */
@keyframes metallicShift {
  0% {
    background-position: 0% 50%;
    filter: brightness(1.1) contrast(1);
  }
  50% {
    background-position: 100% 50%;
    filter: brightness(1.3) contrast(1.1);
  }
  100% {
    background-position: 0% 50%;
    filter: brightness(1.1) contrast(1);
  }
}

@keyframes gridGlow {
  0% { opacity: 0.03; filter: hue-rotate(0deg); }
  50% { opacity: 0.07; filter: hue-rotate(15deg); }
  100% { opacity: 0.03; filter: hue-rotate(0deg); }
}

/* ===================================
   3) LAYOUT: .App CONTAINER
   =================================== */
.App {
  /* If you'd like the .App container to manage your main layout: */
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  min-height: 100vh; 
  /* Position relative if you need stacking context within .App */
  position: relative;
  z-index: 1;
}

/* ===================================
   4) MAIN CONTENT AREA
   =================================== */
main {
  width: 100%;
  max-width: 1200px;
  margin-top: 80px; /* If your header is fixed or large */
  flex-grow: 1;
  position: relative;
  z-index: 2;
  border-radius: 10px;
  padding: 2rem;
}

/* ===================================
   5) FOOTER
   =================================== */
footer {
  text-align: center;
  padding: 1rem;
  background: #006666; /* Slightly darker teal for better contrast */
  color: white;
  width: 100%;
  box-shadow: 0 -2px 6px rgba(0, 0, 0, 0.2);
  position: relative;
  z-index: 2;
}

/* ===================================
   6) RESPONSIVE DESIGN
   =================================== */
/* Tablet: up to 768px */
@media (max-width: 768px) {
  main {
    /* Only add extra top padding if the header is absolutely positioned or fixed */
    padding-top: 100px;
  }

  /* If your Header component uses a .header class, style it here */
  header {
    padding: 0.8rem;
  }
}

/* Mobile: up to 480px */
@media (max-width: 480px) {
  .section {
    padding: 1rem;
    font-size: 0.9rem;
  }

  footer {
    font-size: 0.9rem;
  }
}