/* ===================================
   1) TOOLS SECTION: LAYOUT & SPACING
   =================================== */
.tools-section {
  padding: 2rem;
  /* If you have a global .section style in Section.css,
     you can override or extend it here. */
}

/* Scenario Buttons */
.tools-scenarios {
  margin-bottom: 1.5rem;
  text-align: center;
}

.tools-scenarios button {
  margin: 0.5rem;
  padding: 0.75rem 1.5rem;
  background-color: #008080;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-weight: bold;
  transition: background 0.3s ease-in-out;
}

.tools-scenarios button:hover {
  background-color: #005959;
}

/* ===================================
   2) INPUT SLIDERS & LABELS
   =================================== */
.tools-inputs {
  margin-top: 1rem;
  margin-bottom: 2rem;
}

.input-group {
  margin-bottom: 1rem;
  text-align: left; /* Align labels left */
}

.input-group label {
  display: block;
  font-weight: bold;
  margin-bottom: 0.5rem;
  color: #333;
}

.input-group input[type="range"] {
  width: 100%;
  cursor: pointer;
  accent-color: #008080; /* Modern browsers can style the slider thumb */
}

/* ===================================
   3) TOOLTIP
   =================================== */
.tooltip {
  background-color: #f9f9f9;
  color: #333;
  border: 1px solid #ddd;
  padding: 0.5rem;
  border-radius: 4px;
  margin-top: 1rem;
  text-align: center;
  max-width: 300px;
  margin-left: auto;
  margin-right: auto;
  font-size: 0.9rem;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
}

/* ===================================
   4) CHART CONTAINER
   =================================== */
.tools-chart-container {
  margin-top: 2rem;
  background: #f9f9f9;
  padding: 1rem;
  border-radius: 8px;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
}

/* ===================================
   5) COMPARISON & SUMMARY
   =================================== */
.tools-comparison {
  display: flex;
  flex-wrap: wrap; /* Allows wrapping on smaller screens */
  gap: 1.5rem;
  margin-top: 1rem;
  justify-content: center; /* centers items horizontally */
}

.comparison-box {
  flex: 1 1 300px; /* grows/shrinks, min-width ~300px */
  padding: 1rem;
  background: #f9f9f9;
  border: 1px solid #ddd;
  border-radius: 8px;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
  text-align: center;
}

.tools-summary {
  margin-top: 2rem;
  text-align: left;
}

.tools-summary p {
  margin-bottom: 1rem;
}

/* ===================================
   6) RESPONSIVE DESIGN
   =================================== */
@media (max-width: 768px) {
  .tools-section {
    padding: 1.5rem;
  }
}

@media (max-width: 480px) {
  .tools-section {
    padding: 1rem;
  }

  .tools-scenarios button {
    font-size: 0.9rem;
    padding: 0.5rem 1rem;
  }

  .comparison-box {
    margin-bottom: 1rem;
  }
}