/* ========== PRINCIPLES SECTION STYLING ========== */
.principles-container {
  margin-top: 1.5rem;
}

/* Individual Principle Card */
.principle {
  margin-bottom: 1rem;
  padding: 1rem 1.2rem;
  background: #f9f9f9;
  border: 1px solid #ddd;
  border-radius: 8px;
  box-shadow: 0 2px 6px rgba(0, 128, 128, 0.2);
  transition: all 0.3s ease-in-out;
}

.principle:hover {
  background: #eefaf8;
  transform: translateY(-2px);
  box-shadow: 0 4px 10px rgba(0, 128, 128, 0.3);
}

/* Principle Titles */
.principle h3 {
  display: flex;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
  color: #008080;
  margin-bottom: 0.5rem;
  transition: color 0.3s ease;
  font-size: 1.25rem;
}

.principle h3:hover {
  color: #005959;
}

/* Expand/Collapse Indicator */
.principle h3 span {
  font-size: 1.4rem;
  font-weight: bold;
}

/* Principle Summary (always visible) */
.principle p {
  margin: 0.5rem 0;
  font-weight: 500;
}

/* Expanded Details Wrapper */
.principle-details {
  margin-top: 1rem;
  padding-top: 0.5rem;
  border-top: 1px solid #ddd;
}

/* Subheadings inside expanded content */
.principle-details h4 {
  color: #008080;
  font-size: 1.1rem;
  margin-bottom: 0.5rem;
  margin-top: 1rem;
}

/* Lists */
.benefits-list,
.implementation-list {
  list-style-type: disc;
  margin-left: 1.5rem;
  margin-bottom: 0.5rem;
}

.benefits-list li,
.implementation-list li {
  margin-bottom: 0.5rem;
  color: #333;
}

.example-text {
  margin-top: 0.5rem;
  font-style: italic;
  color: #444;
}

/* ========== RESPONSIVE BREAKPOINTS ========== */

/* Tablet and below */
@media (max-width: 768px) {
  .principle {
    padding: 0.8rem 1rem;
  }

  .principle h3 {
    font-size: 1.1rem;
  }
}

/* Mobile */
@media (max-width: 480px) {
  .principle {
    padding: 0.6rem 0.8rem;
  }

  .principle h3 {
    font-size: 1rem;
  }
}