/* Section Styling */
.section {
  padding: 2rem;
  margin: 1rem auto;
  max-width: 900px;
  background: white;
  border-radius: 8px;
  box-shadow: 0 4px 12px rgba(0, 128, 128, 0.7); /* Updated shadow to match section headings */
  text-align: left;
  transition: transform 0.3s ease-in-out, box-shadow 0.3s ease-in-out;
}

.section:hover {
  transform: translateY(-3px);
  box-shadow: 0 6px 16px rgba(0, 128, 128, 0.9); /* Slightly deeper teal shadow on hover */
}

/* Section Headings */
.section h2 {
  font-size: 1.8rem;
  color: #008080;
  margin-bottom: 1rem;
  text-align: center;
}

.section h3 {
  font-size: 1.6rem;
  color: #333;
  margin-bottom: 1rem;
  text-align: center;
}

.section h4 {
  font-size: 1.4rem;
  color: #333;
  margin-bottom: 1rem;
  text-align: center;
}

/* Paragraph Styling */
.section p {
  font-size: 1rem;
  line-height: 1.6;
  color: #333;
  margin-bottom: 1rem;
}

/* List Styling */
.section ul {
  padding-left: 1.5rem;
  list-style-type: square;
}

.section ul li {
  font-size: 1rem;
  margin-bottom: 0.5rem;
  font-weight: 500;
}

/* Responsive Styling */
@media (max-width: 768px) {
  .section {
    padding: 1.5rem;
  }

  .section h2 {
    font-size: 1.6rem;
  }
}

@media (max-width: 480px) {
  .section {
    padding: 1rem;
  }

  .section h2 {
    font-size: 1.4rem;
  }

  .section p,
  .section ul li {
    font-size: 0.9rem;
  }
}